@charset "UTF-8";

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  display: block;
  max-width: 100%;
}

a {
  list-style: none;
  text-decoration: none;
  cursor: pointer;
}

/* переменные*/

:root {
  --text-bl: #2f2e2e;
  --text-wh: #fafafa;
  --background: #f4f6f5;
}

body {
  font-family: "Playfair Display", serif;
}

.contain {
  max-width: 1280px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

/*Header*/

.nav {
  display: flex;
  justify-content: space-between;
  padding-top: 47px;
  margin-bottom: 91px;
}

.header__logotype {
  font-family: "Barlow", sans-serif;
  font-size: 42px;
  line-height: 50px;
  font-weight: 600;
}

.header__logotype-sm {
  font-size: 28px;
  font-family: "Barlow", sans-serif;
  font-weight: 500;
  padding-top: 15px;
  padding-left: 40px;
}

.burger {
  display: none;
}

.header__menu {
  display: flex;
  align-items: end;
  padding-bottom: 10px;
}

.header__menu__item {
  font-family: "Inter", sans-serif;
  padding-left: 80px;
  font-size: 20px;
  line-height: 24px;
}

.header__menu__item a {
  color: var(--text-bl);
}

.header__menu__item a:hover {
  font-weight: 700;
}

.header {
  width: 1150px;
  width: 100%;
  height: 740px;
  height: 100%;
  background-image: url("../img/header-img.png");
  background-repeat: no-repeat;
  background-size: auto;
  background-position: right;
}

.header__content {
  padding-top: 95px;
  padding-bottom: 95px;
}

.header__content__card {
  padding: 139px 160px 0px 100px;
  max-width: 690px;
  height: 550px;
  background-color: rgba(244, 246, 245, 0.5);
}

.header__content__card p {
  padding-top: 10px;
  font-family: "Inter", sans-serif;
  font-size: 32px;
  line-height: 39px;
  font-weight: 200;
  color: var(--text-bl);
}

.title-h2 {
  font-size: 51px;
  line-height: 60px;
  font-weight: 600;
  color: var(--text-bl);
}

.sidenav li > a {
  font-family: "Inter", sans-serif;
  font-size: 20px;
}

/*Services*/

.services {
  background-color: rgba(37, 77, 77, 0.2);
}

.services__title {
  padding-top: 16px;
  margin-bottom: 42px;
}

.services__items {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 65px;
}

.services__title {
  text-align: center;
}

.services__item h4,
.services__item p {
  text-align: center;
}

.services__item__img img {
  display: block;
  margin: auto;
}

.services__item__title {
  font-size: 27px;
  line-height: 32px;
  font-weight: 600;
  padding-top: 10px;
  margin-bottom: 10px;
}

.services__item__text {
  font-size: 16px;
  line-height: 19px;
  max-width: 160px;
}

/*About*/

.about {
  padding-top: 76px;
  margin-bottom: 76px;
}

.about__wrapper {
  display: flex;
  justify-content: space-between;
}

.about__content {
  padding-left: 20px;
  max-width: 476px;
}

.about__content a {
  font-family: "Inter", sans-serif;
  font-size: 24px;
  font-weight: 500;
  color: var(--text-bl);
}

.about__content a:hover {
  font-weight: 700;
}

.about__content__title {
  padding-top: 65px;
}

.about__content__text {
  padding-top: 27px;
  font-size: 31px;
  line-height: 36px;
  color: var(--text-bl);
  margin-bottom: 36px;
}

.mod__content__title {
  font-size: 38px;
  line-height: 50px;
  font-weight: 500;
  color: var(--text-bl);
  text-align: center;
  margin-bottom: 20px;
}

.modal-content p {
  font-size: 22px;
  line-height: 36px;
  color: var(--text-bl);
}

.mod-a {
  font-style: 30px;
  text-decoration: none;
}

/*Projects*/

.progects__title {
  margin-bottom: 72px;
}

.portfolio__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-between;
  align-items: center;
  row-gap: 85px;
  column-gap: 105px;
  margin-bottom: 72px;
}

.progects__item__text {
  padding-top: 12px;
  max-width: 180px;
  margin: 0 auto;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
}

/*Testimonial*/

.testimonial__title {
  padding-top: 20px;
  text-align: center;
}

.testimonial__items {
  padding-top: 80px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 130px;
}

.testimonial__item {
  display: flex;
}

.testimonial__item__img img {
  margin-right: 25px;
}

.testimonial__item__name {
  padding-top: 5px;
  font-size: 28px;
  line-height: 33px;
  font-weight: 600;
}

.testimonial__item__text {
  padding-top: 16px;
  font-size: 17px;
  line-height: 20px;
  max-width: 310px;
}

/*Footer*/

.footer {
  background-color: var(--text-bl);
}

.footer__wrapper {
  padding-top: 47px;
  display: flex;
  justify-content: space-between;
  color: var(--text-wh);
  padding-bottom: 50px;
}

.footer__col {
  max-width: 295px;
  margin-right: 15px;
}

.footer__col:last-child {
  margin-right: 0;
}

.footer-logo {
  color: #fafafa;
  margin-bottom: 15px;
}

.links {
  display: flex;
  padding-top: 30px;
}

.links img {
  margin-right: 40px;
}

.col__title {
  padding-top: 18px;
  font-size: 24px;
  line-height: 29px;
  font-family: "Inter", sans-serif;
  font-weight: 700;
  margin-bottom: 18px;
}

.col-text,
.col2-text {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 17px;
  line-height: 29px;
  max-width: 287px;
}

.col2-text {
  line-height: 25px;
  margin-bottom: 10px;
}

.location {
  max-width: 295px;
  width: 100%;
  height: 221px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.location iframe {
  max-width: 295px;
}

/*Media*/

@media screen and (max-width: 1200px) {
  .portfolio__grid {
    column-gap: 40px;
  }
}

@media screen and (max-width: 1025px) {
  .about__content__title {
    padding-top: 0;
  }
}

@media screen and (max-width: 960px) {
  .header__menu__item {
    padding-left: 30px;
  }

  .about__content__text {
    padding-top: 18px;
    margin-bottom: 20px;
    font-size: 22px;
    line-height: 30px;
    max-width: 420px;
  }

  .portfolio__grid {
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }

  .testimonial__items {
    flex-direction: column;
    align-items: center;
    margin-bottom: 80px;
  }

  .testimonial__item {
    margin-bottom: 40px;
  }

  .testimonial__item:last-child {
    margin-bottom: 0;
  }

  .testimonial__item__name {
    max-width: 310px;
  }
}

@media screen and (max-width: 767px) {
  .footer__wrapper {
    flex-wrap: wrap;
    justify-content: space-evenly;
  }

  .footer__col {
    margin-right: 0;
  }

  .footer__col:last-child {
    padding-top: 30px;
  }

  .modal-content p {
    font-size: 18px;
    line-height: 26px;
  }
}

@media screen and (max-width: 676px) {
  .nav {
    padding-top: 35px;
    margin-bottom: 35px;
  }

  .header__logotype {
    margin-bottom: 20px;
  }

  .header__menu {
    display: none;
  }

  .burger {
    display: block;
    margin-top: 15px;
  }

  .header__content__card {
    padding: 80px 60px;
  }

  .services__items {
    flex-direction: column;
  }

  .services__item {
    margin-bottom: 30px;
  }

  .services__item:last-child {
    margin-bottom: 0;
  }

  .about__wrapper {
    flex-direction: column;
    align-items: center;
  }

  .about__content__title {
    padding-top: 20px;
    text-align: center;
  }

  .about__a {
    padding-top: 15px;
    text-align: center;
  }

  .progects__title {
    text-align: center;
  }

  .portfolio__grid {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media screen and (max-width: 560px) {
  .title-h2 {
    font-size: 36px;
  }

  .header__content__card P {
    font-size: 24px;
  }

  .header__logotype {
    font-size: 32px;
  }

  .testimonial__item {
    flex-direction: column;
    align-items: center;
  }

  .footer__wrapper {
    flex-direction: column;
    align-items: center;
  }

  .footer__col {
    padding-top: 20px;
  }

  .footer__col:first-child {
    padding-top: 0;
  }

  .footer__col:last-child {
    padding-top: 20px;
  }

  .f-col2 {
    padding-right: 98px;
  }
}

@media screen and (max-width: 410px) {
  .header__content__card {
    padding: 40px 30px;
  }
}

@media screen and (max-width: 330px) {
  .location iframe {
    max-width: 240px;
  }

  .f-col2 {
    padding-right: 60px;
  }
}